import { Button, makeStyles } from '@material-ui/core';
import './index.css';

const dataArray = [
  {
    type : "Hero_SKIN",
    value : "skin_2"
  },
  {
    type : "AK_47",
    value : "AK47Skin1"
  },
  {
    type : "SHOT_GUN",
    value : "ShotgunSkin3"
  },
  {
    type : "PISTOL",
    value : "PistolSkin4"
  },
  {
    type : "KNIFE",
    value : "KnifeSkin3"
  }
];



function App() {

  const useStyles = makeStyles(() => ({
    btn : {
      margin : "10px"
    },

  }));

  const classes = useStyles();

  const onBtnClick = (id)=>{
    window.UnityInAppBrowser.sendMessageFromJS(id);
  };


  return (
    <div className="App">
        <Button className={classes.btn} onClick={()=>onBtnClick(JSON.stringify(dataArray[0]))} variant="contained" color="primary">
          Hero 2
        </Button>
        <Button className={classes.btn} onClick={()=>onBtnClick(JSON.stringify(dataArray[1]))} variant="contained" color="primary">
          AK-47_1
        </Button>
        <Button className={classes.btn} onClick={()=>onBtnClick(JSON.stringify(dataArray[2]))} variant="contained" color="primary">
          ShotGun 3
        </Button>
        <Button className={classes.btn} onClick={()=>onBtnClick(JSON.stringify(dataArray[3]))} variant="contained" color="primary">
          Pistol 4
        </Button>
        <Button className={classes.btn} onClick={()=>onBtnClick(JSON.stringify(dataArray[4]))} variant="contained" color="primary">
          Knife 3
        </Button>
    </div>
  );
}

export default App;
